html{font-size:10px;margin:0;height:100%;background-color:#0f0e16;-user-select:all;-webkit-user-select:all;-webkit-user-select:text;-moz-user-select:all;-ms-user-select:all}@media (min-width: 780px){html{font-size:14px}}body{color:#fff;font-family:sans-serif;font-size:1.8em;line-height:1.4;font-family:'Overpass', sans-serif;display:flex;flex-direction:column;justify-content:center;min-height:100%}.wrapper{margin:50px auto;max-width:600px}header{margin-bottom:2em}header .logo{display:block;width:60%;max-width:260px;margin:0 auto}header h1{font-weight:800;text-align:center;font-size:6rem;text-transform:uppercase;line-height:0.5;margin:5rem 0 0}header h1 small{font-size:2rem}nav{display:flex;margin:1rem}nav a{flex:1;margin-right:1rem;color:#fff;text-align:center;text-transform:uppercase;text-decoration:none;border-radius:.8em;font-weight:800;font-size:.8em;padding:.5em 1em;background-color:#269489;transition:background-color 300ms ease-in-out}nav a:hover{background-color:#1c6b63}nav a:first-child{background-color:#2694a8}nav a:first-child:hover{background-color:#1d6f7e}nav a:last-child{margin-right:0;background-color:#28956c}nav a:last-child:hover{background-color:#1d6d4f}main h3{margin:2.2rem auto;text-align:center}
